#filter-component {
  width: 100%;
  height: auto;

  display: flex;
  flex-flow: column nowrap;
  place-items: center stretch;
  place-content: center center;
  
  box-sizing: border-box;
  padding: var(--default-size-medium, 24px);
  gap: var(--default-size-medium, 24px);

  font: normal calc(var(--default-size-medium, 24px) * 0.75) var(--default-font-family, "Outfit", sans-serif);

  > :nth-child(1) {
    position: relative;

    display: inline-block;

    width: 100%;
    height: auto;

    box-sizing: border-box;
    border-radius: calc(var(--default-size-small, 1px) * 4.00);
    padding: calc(var(--default-size-medium, 24px) * 0.50) calc(var(--default-size-medium, 24px) * 2.00) calc(var(--default-size-medium, 24px) * 0.50) calc(var(--default-size-medium, 24px) * 0.50);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

    > input {
      width: 100%;
      height: auto;

      border: none;

      cursor: pointer;

      font: normal calc(var(--default-size-medium, 24px) * 0.75) var(--default-font-family, "Outfit", sans-serif);

      &:focus-visible {
        outline: none;
      }
    }

    > :nth-child(2) {
      position: absolute;
      right: 0;
      top: 0;

      display: block;

      box-sizing: border-box;
      padding: calc(var(--default-size-medium, 24px) * 0.50);

      cursor: pointer;

      > * {
        box-sizing: border-box;
        border-radius: calc(var(--default-size-small, 1px) * 4.00);
        padding: calc(var(--default-size-small, 1px) * 2.00);

        background-color: var(--default-color-theme, blue);

        font-size: calc(var(--default-size-medium, 24px) * 0.75);
        
        color: var(--default-color-light, white);
      }
    }
  }

  > :nth-child(2) {
    display: flex;
    flex-flow: row nowrap;
    place-items: center center;
    place-content: center center;

    width: 100%;
    height: auto;

    box-sizing: border-box;
    gap: var(--default-size-medium, 24px);

    > * {
      position: relative;

      display: inline-block;

      flex: 1 1 0;
      height: auto;

      box-sizing: border-box;
      border-radius: calc(var(--default-size-small, 1px) * 4.00);
      padding: calc(var(--default-size-medium, 24px) * 0.50) calc(var(--default-size-small, 1px) * 4.00) calc(var(--default-size-medium, 24px) * 0.50) calc(var(--default-size-medium, 24px) * 0.50);
      box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;  

      > * {
        width: 100%;
        height: auto;
      }

      > :nth-child(1) {
        display: flex;
        flex-flow: row nowrap;
        place-items: center center;
        place-content: center space-between;

        > :nth-child(1) {
          flex: 1 1 0;
          height: auto;
        }
  
        > :nth-child(2) {
          box-sizing: border-box;
          padding: 0 calc(var(--default-size-medium, 24px) * 0.25);

          font-size: calc(var(--default-size-medium, 24px) * 0.75);
        }
      }

      &.filter-menu-dropdown-inactive {
        > :nth-child(2) {
          display: none;
        }
      }

      &.filter-menu-dropdown-active {
        > :nth-child(2) {
          position: absolute;
          left: 0;
          top: calc(var(--default-size-medium, 24px) * 3.50);
          z-index: 100;

          overflow-x: hidden;
          overflow-y: scroll;

          display: block;

          max-height: var(--default-size-large, 480px);

          box-sizing: border-box;
          border-radius: calc(var(--default-size-small, 1px) * 4.00);
          padding: calc(var(--default-size-small, 1px) * 4.00) calc(var(--default-size-small, 1px) * 4.00) calc(var(--default-size-small, 1px) * 8.00) calc(var(--default-size-small, 1px) * 4.00);
          box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;  
        
          background-color: var(--default-color-light, white);

          > * {
            box-sizing: border-box;
            padding: calc(var(--default-size-medium, 24px) * 0.50);
          }

          :hover, &.filter-menu-item-selected {
            color: var(--default-color-theme, blue);
          }
        }
      }
    }

